
import { defineComponent } from "vue";
import Footer from "@/components/Footer.vue";
import Header from "@/components/Header.vue";
import { CommonScriptService } from "@/services/CommonScriptService";
import { showDetailedSymptomsApi } from "@/api/detailed-symptoms";

import { showDetailedFlavorsApi } from "@/api/detailed-flavors";
import { showDetailedTastesApi } from "@/api/detailed-tastes";


export default defineComponent({
  name: "2",
  watch: {},
  data() {
    return {
      isShow: false,
      isSecondShow: false,
      backPath: "Diagnose2",
      symptoms: [] as any[],
      flavors: [] as any[],
      tastes: [] as any[],

      flavorIds: [] as any[],
      tasteIds: [] as any[],
      // currentPath: 'Diagnose3',
      nextPath: "DiagnoseResult",
      nextPath1: "Diagnose4",

      condition: 0 as number,
      condition1: 0 as number,

      taste: 0 as number,
      diagnoseData: {} as any,
      commonScriptService: new CommonScriptService(),
    };
  },
  components: {
    Footer,
    Header,
  },
  created: async function () { },
  mounted: function (): void {
    document.body.className = "diagnose";

    this.init();
    this.isShow = true;
    this.isSecondShow = false;
    this.$nextTick(function () {
      this.commonScriptService.execute();
    });
  },

  methods: {
    init: async function () {

      const diagnoseJsonData = localStorage.getItem("diagnoseJsonData");
      if (!diagnoseJsonData) {
        this.$router.push({
          name: "Diagnose0",
        });
        return;
      }
      const diagnoseData = JSON.parse(diagnoseJsonData);
      if (!diagnoseData?.diagnose2) {
        this.$router.push({
          name: this.backPath,
        });
        return;
      }
      this.diagnoseData = diagnoseData;
      console.log("detailedSymptomsID:", this.diagnoseData.diagnose2.condition);

      const DetailedSymptomsApiResult = await showDetailedSymptomsApi(Number(this.diagnoseData.diagnose2.condition));
      console.log("detailedSymptoms:", DetailedSymptomsApiResult);

      if (!DetailedSymptomsApiResult.success) {
        this.commonError(DetailedSymptomsApiResult);
        return;
      }
      this.symptoms = DetailedSymptomsApiResult.data

      if (this.symptoms[0].name === "味と香りの好みを選択してください") {
        this.isShow = false;
        const result = await showDetailedFlavorsApi(this.symptoms[0].symptom_id);
        console.log("data:", result);
        this.flavors = result.data;

        const result1 = await showDetailedTastesApi(this.symptoms[0].symptom_id);
        console.log("data:", result1);
        this.tastes = result1.data;

        this.isSecondShow = true;
      }
    },

    nextPage: function () {
      if (this.isShow === true) {
        if (this.condition === 0) {
          return;
        }
        this.diagnoseData.diagnose3 = {
          id: this.diagnoseData.diagnose2.condition,
          condition: this.condition,
        };
        localStorage.setItem(
          "diagnoseJsonData",
          JSON.stringify(this.diagnoseData)
        );
        console.log("diagnoseData:::::", this.diagnoseData.diagnose3);
        if (this.diagnoseData.diagnose3.condition === '当てはまるものがない' || this.diagnoseData.diagnose3.condition === "分からない") {
          this.$router.push({
            name: this.nextPath1,
          });
        }
        else {
          this.$router.push({
            name: this.nextPath,
          });
        }
      }
      if (this.isSecondShow === true) {
        const query: string =
          "/diagnoseproduct?" +
          "taste_ids=" +
          this.tasteIds +
          "&flavor_ids=" +
          this.flavorIds +
          "&material_ids=" +
          "" +
          "&symptom_ids=" +
          "" +
          "&order_by=" +
          "" +
          "&recommendation_kind=" +
          "";
        location.href = query;
      }
    },
  },
});
